.cards {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  max-width: 1024px;
  margin: 0 auto;
  padding: 50px 40px;

  &__card {
    margin-right: 48px;
    margin-bottom: 46px;
  }

  &__card:nth-last-child(-n+4) {
    margin-bottom: 0;
  }
  
  &__card:nth-child(4n) {
    margin-right: 0;
  }

}
